<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row mb-4">
      <div class="col-12">
        <div class="card">
          <div class="card-body">
              <div class="row">
                  <div class="col-md-12">
                    <div class="form-group">
                      <label for="validationCustom01">{{
                        $t("article.title")
                      }}</label>
                      <input
                        id="validationCustom01"
                        type="text"
                        class="form-control"
                        :placeholder="
                          $t('article.title')
                        "
                        v-model="form.article_title"
                        :class="{
                          'is-invalid': submitted && $v.form.article_title.$error,
                        }"
                      />
                      <div
                        v-if="submitted && $v.form.article_title.$error"
                        class="invalid-feedback"
                      >
                        <span v-if="!$v.form.article_title.required">{{
                          $t("form.error.text")
                        }}</span>
                      </div>
                    </div>
                  </div>  
                  <div class="col-md-12">
                    <div class="form-group">
                      <label for="validationCustom01">{{
                        $t("article.desc")
                      }}</label>
                      <!-- <input
                        id="validationCustom01"
                        type="text"
                        class="form-control"
                        :placeholder="
                          $t('article.desc')
                        "
                        v-model="form.article_desc"
                        :class="{
                          'is-invalid': submitted && $v.form.article_desc.$error,
                        }"
                      /> -->
                        <ckeditor class="ck"  v-model="form.article_desc" :editor="editor"  :class="{
                          'is-invalid': submitted && $v.form.article_desc.$error,
                        }"></ckeditor>
                      <div
                        v-if="submitted && $v.form.article_desc.$error"
                        class="invalid-feedback"
                      >
                        <span v-if="!$v.form.article_desc.required">{{
                          $t("form.error.text")
                        }}</span>
                      </div>
                    </div>
                  </div>   
                  <div class="col-md-12">
                  <div class="form-group">
                      <label for="validationCustom01">{{
                        $t("article.image")
                      }}</label>

                        <b-form-file
                          :state="true"
                          :placeholder="$t('user-management.choose')"
                          :drop-placeholder="$t('user-management.drop')"
                          @change="ImageTarget"
                          accept="image/*"
                          :class="{
                          'is-invalid': submitted && $v.form.article_pict.$error,
                        }"
                      >
                      </b-form-file>
                      <div
                        v-if="submitted && $v.form.article_pict.$error"
                        class="invalid-feedback"
                      >
                        <span v-if="!$v.form.article_pict.required">{{
                          $t("form.error.text")
                        }}</span>
                      </div>
                    </div>
                  </div> 
                  <div class="col-md-12 text-right">
                     <router-link to="/article">
                        <b-button size="md" class="mr-1" variant="danger">
                          {{$t('button.cancel.text')}}
                        </b-button>
                      </router-link>
                      <b-button size="md" variant="success" @click="addArticles">
                        {{$t('button.save.text')}}
                      </b-button>
                  </div>
              </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import Vue from "vue";
import Swal from "sweetalert2";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import { required, sameAs } from "vuelidate/lib/validators";
import UserService from "@/helpers/user.service";
import ArticleService from "@/helpers/article.service";
import CKEditor from "@ckeditor/ckeditor5-vue";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import store from '@/state/store';
import moment from "moment";

export default {
  page: {
    meta: [{ name: "description", content: appConfig.description }],
  },
  components: { Layout, PageHeader,  ckeditor: CKEditor.component },
  data() {
    return {
      urlImage: process.env.VUE_APP_BASE_API_URL_IMAGE,
      user_type: store.state.authBackend.user.type,
      title: this.$t("article.article-management"),
      items: [
        {
          text: this.$t("menuitems.dashboards.text"),
          href: "#",
        },
        {
          text: this.$t("article.article-management"),
          active: true,
        },
      ],
      editor: ClassicEditor,
      editorData:"<h5></h5>",
      form: {
          "article_desc" : "",
          "article_title" : "",
          "article_pict" : "",

      },
      file:{
        "article_pict": '',
        "name": ''
      },
      submitted: false,
    };
  },
  validations: {
    form: {
        article_desc: { required },
        article_title: { required },
        article_pict: { required }
    },
  },
  methods: {
    list() {
      // this.loading = true;
      UserService.getProfile()
      .then(response => {
        // console.log( response.data.data)
        this.user_type = response.data.data.admin.role
      })
      .catch(e => {
        this.message = e.response.data.data
      });
    },

    ImageTarget(evt){
      this.form.article_pict = evt.target.files[0];
    },

    ImageEditTarget(evt){
      this.file.article_pict = evt.target.files[0];
      this.file.name = evt.target.files[0].name;
    },

    addArticles(e) {
      console.log('gooo');
      // this.loadingAdd = true;
      this.submitted = true;
      this.$v.form.$touch();
      if (
        this.form.article_desc == ""  || 
        this.form.article_title == "" || 
        this.form.article_pict == ""
      ) {
        e.preventDefault();
      } else {
        const formImages = new FormData();
        formImages.append('article_title', this.form.article_title)
        formImages.append('article_desc', this.form.article_desc)
        formImages.append('article_pict', this.form.article_pict, this.form.article_pict.name);
        console.log(formImages);
        ArticleService.addArticle(formImages)
          .then((response) => {
            Vue.swal({
              position: "top-end",
              icon: "success",
              title: response.data.message,
              showConfirmButton: false,
              timer: 1000,
            });
           this.$router.push('/article');
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
  },
  beforeMount(){
    this.list();
  },
};
</script>

<style scoped>
.badge {
  border-radius: 2%;
}
.img-fluid{
  height: 100px;
}
.ck {
  height: 400px;
}
</style>
